.slider {
   height: calc(100vh - 80px); 
   width: 100vw;
   position: relative;
   overflow: hidden;

   .container {
        width: 300vw;
        display: flex;
        height: 100%;
        transition: all 1s ease;
        

        img {
            width: 100vw;
            height: 100%;
            object-fit: cover;
        }
   }

   .icons {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    width: fit-content;
    gap: 10px;
    bottom: 100px;
    display: flex;

    .icon {
        width: 50px;
        height: 50px;
        border: 1px solid gray;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }
   }
}